  div {
    position:relative;
    z-index: 2;
  }

  body {
    transition: ease-in color 2000;
    background-color: #333;
    color: #333;
    font-family: 'Raleway', sans-serif;
    font-weight:400;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }  
  .footer {
    width:450px;
    text-align:center;
    display:block;
    margin:15px auto 30px auto;
    font-size:0.8em;
    color:#fff;
  }
  a {
      font-weight:500;
      text-decoration:none;
      color:#fff;
    }
  

  #quote-box {
    border-radius:3px;
    position:relative;
    margin:8% auto auto auto;
    width:450px;
    padding:40px 50px;
    display:table;
    background-color:#fff;
  }

  .quote-text {
    font-size:1.0em;
    margin-right: 0.4em;
    text-align:center;
    width:450px;
    height:auto;
    clear:both;
    font-weight:500;
    font-size:1.75em;
  }

  .quote-author {
    width:450px;
    height:auto;
    clear:both;
    padding-top:20px;
    font-size:1em; 
    text-align:right;
  }

  .buttons-div {
    width:450px;
    margin:auto;
    display:block;
  }
  .button{
      height:38px;
      border:none;
      border-radius:3px;
      color:#fff;
      background-color:#333;
      outline:none;
      font-size:0.85em;
      padding: 8px 18px 6px 18px;
      margin-top:30px;
      opacity:1;
      cursor:pointer;
  }
  .buttons-div .button:hover {
    opacity:0.9;
  }

  #tweet-quote {
    float:left;
    padding:0px;
    padding-top:8px;
    text-align:center;
    font-size:1.2em;
    margin-right:5px;
    height:30px;
    width:40px;
  }
  #tumblr-quote{
    float:left;
    padding:0px;
    padding-top:8px;
    text-align:center;
    font-size:1.2em;
    margin-right:5px;
    height:30px;
    width:40px;
  }
  #new-quote {
    float:right;
  }
  @media only screen and (max-width: 768px) {
    /* For mobile phones: */
  .quote-text {
  margin: 0;
  }
  .footer{
    margin: 20px 0px;
    width: 100%;
    font-size: 1.2em;
  }
  #quote-box {
  margin: 0;
  width: 100%;
  padding: 5px;
  }
  .quote-text{
    width: 100%;
  }
  .quote-author{
    width: 100%;
  }
  .buttons-div{
    width: 100%;
  }
  }
